import { Input } from '@material-tailwind/react';
import React, { useEffect, useState } from 'react';
import CustomButton from './CustomButton';
import { XCircleIcon } from '@heroicons/react/24/solid';

export default function CustomInput({ labelText, icon, disabled, errorMessage, value, onInputChange }) {
    const [errorChk, setErrorChk] = useState(errorMessage ? 'error' : '');
    const [inputValue, setInputValue] = useState('');
    const [isFocused, setIsFocused] = useState(false); 

    useEffect(() => {
        setErrorChk(errorMessage ? 'error' : '');
    }, [errorMessage]);

    useEffect(() => {
        setInputValue(value);
    }, [value]);

    const handleInputChange = (event) => {
        setInputValue(event.target.value);
        onInputChange(inputValue);
    };

    const handleClearInput = () => {
        setInputValue('');
        setErrorChk('');
    };

    const handleFocus = () => {
        setIsFocused(true);
    };

    const handleBlur = () => {
        setIsFocused(false);
    };

    const iconClass = `
        absolute
        top-3
        ${icon && icon[1] === 'right' ? `
            ${errorChk ? 'right-8' : 'right-3'}
        `: `
            left-3
        `}
        ${isFocused ? 'text-gray-300' : ''}
        ${disabled ? 'text-gray-300' : ''}
    `

    const inputClass = `
        hover:bg-gray-100 caret-blue-300
        ${icon && icon[1] !== 'right' ? 'pl-8' : ''}
    `

    const labelClass = `
        ${icon && icon[1] !== 'right' ? 'pl-0 peer-placeholder-shown:pl-5 peer-focus:pl-0' : ''}
    `

    return (
        <div className='relative'>
            <Input
                className={inputClass}
                label={labelText}
                labelProps={{className: labelClass}}
                disabled={disabled}
                value={inputValue}
                onChange={handleInputChange}
                onFocus={handleFocus}
                onBlur={handleBlur}
                error={errorChk === 'error'}
            />
            {icon && (
                <span className={iconClass}>
                    {icon[0]}
                </span>
            )}
            {errorChk ?
                <>
                    <span className='text-red-400 text-[12px]'>{errorMessage}</span>
                    <CustomButton
                        // disabled={true}
                        btnType={1}
                        className="rounded-50 p-0 border-0 bg-transparent hover:bg-transparent focus:bg-transparent active:bg-transparent !absolute top-3 right-3"
                        clickFunction={handleClearInput}
                        icon={<XCircleIcon className='w-4 h-4 text-red-400' />}
                    />
                </>
                : ''}
        </div>
    );
}

