import { Button } from '@material-tailwind/react';
import React from 'react';

export default function CustomButton({ disabled, clickFunction, btnText, icon, btnType, className, border }) {
    const commonClass = `
        py-3
        px-5
        rounded
        flex
        items-center
        shadow-sm
        shadow-color-[rgba(0,0,0,0.1)]
        hover:shadow-color-[rgba(0,0,0,0.1)]
        ${btnType === 1 ? `
            bg-blue-300
            hover:bg-blue-200
            focus:bg-blue-200
            active:bg-blue-400
            text-white
            ${disabled ? 'disabled:bg-gray-200' : ''}
        ` : ''}
        ${btnType === 2 ? `
            bg-black
            hover:bg-gray-400
            focus:bg-gray-400
            active:bg-gray-500
            text-white
            ${disabled ? 'disabled:bg-gray-200' : ''}
        ` : ''}
        ${btnType === 3 ? `
            bg-white
            hover:bg-blue-100
            focus:bg-blue-100
            active:bg-blue-200
            text-blue-300
            border
            border-blue-300
            ${disabled ? `disabled:bg-white text-gray-200 ${border ? `border-gray-200` : ''}` : ''}
        ` : ''}
        ${btnType === 4 ? `
            bg-white
            hover:bg-gray-100
            focus:bg-gray-100
            active:bg-gray-200
            text-black
            border
            border-black
            ${disabled ? `disabled:bg-white text-gray-200 ${border ? `border-gray-200` : ''}` : ''}
        ` : ''}
        ${btnType === 5 ? `
            bg-transparent
            text-black
            hover:underline
            hover:text-gray-400
            active:text-black
            rounded-0
            p-0
            shadow-none
            hover:shadow-none
        ` : ''}
        ${className}
        `

    return (
        <Button
            className={commonClass}
            disabled={disabled}
            onClick={clickFunction}
        >
            <span>{icon}</span>
            <span>{btnText}</span>
        </Button>
    );
}

